import React, { useState, useEffect } from 'react';
import Helmet from 'react-helmet';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { navLinks } from '@config';
import { Menu } from '@components';
import { IconLogo } from '@components/icons';
import styled from 'styled-components';
import { theme, mixins, media } from '@styles';
const { colors, fontSizes, fonts } = theme;

const NavContainer = styled.header`
  ${mixins.flexBetween};
  position: fixed;
  top: 0;
  padding: 0px 50px;
  background-color: ${props =>
    props.scrolldirection ? colors.underscoreWhiteOp : colors.underscoreWhite};
  transition: ${theme.transition};
  z-index: 11;
  filter: none !important;
  pointer-events: auto !important;
  user-select: auto !important;
  width: 100%;
  height: ${props => (props.scrolldirection === 'none' ? theme.navHeight : theme.navScrollHeight)};
  box-shadow: ${props =>
    props.scrolldirection === 'up' ? `0 10px 30px -10px ${colors.shadowNavy}` : 'none'};
  transform: translateY(
    ${props => (props.scrolldirection === 'down' ? `-${theme.navScrollHeight}` : '0px')}
  );
  ${media.desktop`padding: 0 40px;`};
  ${media.tablet`padding: 0 25px;`};
`;
const Navbar = styled.nav`
  ${mixins.flexBetween};
  position: relative;
  width: 100%;
  color: ${colors.underscoreBlack};
  font-family: ${fonts.SFMono};
  counter-reset: item 0;
  z-index: 12;
`;
const Logo = styled.div`
  ${mixins.flexCenter};
  isolation: isolate;
  mix-blend-mode: multiply;
  path {
    mix-blend-mode: multiply;
  }
`;
const LogoLink = styled(AnchorLink)`
  display: block;
  color: ${colors.green};
  width: 160px;
  height: 40px;
  &:hover,
  &:active {
    svg {
      fill: ${colors.transGreen};
    }
  }
  svg {
    fill: none;
    transition: ${theme.transition};
    user-select: none;
  }
`;
const Hamburger = styled.div`
  ${mixins.flexCenter};
  overflow: visible;
  margin: 0 -12px 0 0;
  padding: 15px;
  cursor: pointer;
  transition-timing-function: linear;
  transition-duration: 0.15s;
  transition-property: opacity, filter;
  text-transform: none;
  color: inherit;
  border: 0;
  background-color: transparent;
  display: none;
  ${media.tablet`display: flex;`};
`;
const HamburgerBox = styled.div`
  position: relative;
  display: inline-block;
  width: ${theme.hamburgerWidth}px;
  height: 24px;
`;
const HamburgerInner = styled.div`
  background-color: ${props =>
    props.menuOpen ? `${colors.underscoreWhite}` : `${colors.underscoreBlack}`};
  position: absolute;
  width: ${theme.hamburgerWidth}px;
  height: 2px;
  border-radius: ${theme.borderRadius};
  top: 50%;
  left: 0;
  right: 0;
  transition-duration: 0.22s;
  transition-property: transform;
  transition-delay: ${props => (props.menuOpen ? `0.12s` : `0s`)};
  transform: rotate(${props => (props.menuOpen ? `225deg` : `0deg`)});
  transition-timing-function: cubic-bezier(
    ${props => (props.menuOpen ? `0.215, 0.61, 0.355, 1` : `0.55, 0.055, 0.675, 0.19`)}
  );
  &:before,
  &:after {
    content: '';
    display: block;
    background-color: ${props =>
    props.menuOpen ? `${colors.underscoreWhite}` : `${colors.underscoreBlack}`};
    position: absolute;
    left: auto;
    right: 0;
    width: ${theme.hamburgerWidth}px;
    height: 2px;
    transition-timing-function: ease;
    transition-duration: 0.15s;
    transition-property: transform;
    border-radius: 4px;
  }
  &:before {
    width: ${props => (props.menuOpen ? `100%` : `100%`)};
    color: ${props => (props.menuOpen ? `${colors.underscoreWhite}` : `${colors.underscoreBlack}`)};
    top: ${props => (props.menuOpen ? `0` : `-10px`)};
    opacity: ${props => (props.menuOpen ? 0 : 1)};
    transition: ${props => (props.menuOpen ? theme.hamBeforeActive : theme.hamBefore)};
  }
  &:after {
    width: ${props => (props.menuOpen ? `100%` : `100%`)};
    bottom: ${props => (props.menuOpen ? `0` : `-10px`)};
    transform: rotate(${props => (props.menuOpen ? `-90deg` : `0`)});
    transition: ${props => (props.menuOpen ? theme.hamAfterActive : theme.hamAfter)};
  }
`;
const NavLinks = styled.div`
  display: flex;
  align-items: center;
  ${media.tablet`display: none;`};
`;
const NavList = styled.ol`
  div {
    ${mixins.flexBetween};
  }
`;

const NavListItem = styled.li`
  margin: 0 10px;
  position: relative;
  font-size: ${fontSizes.smallish};
  counter-increment: item 1;
  // background-image: linear-gradient(120deg, #ff00b0 0%, #8fd3f4 100%);
  // background-repeat: no-repeat;
  // background-size: 100% 0.2em;
  // background-position: 0 88%;
  transition: background-size 0.25s ease-in;
  &:hover {
    background-size: 100% 88%;
  }
  &:after {
    display: none !important;
  }
  // &:before {
  //   content: '0' counter(item) '.';
  //   text-align: right;
  //   color: ${colors.green};
  //   font-size: ${fontSizes.xsmall};
  // }
`;

const NavLink = styled(AnchorLink)`
  padding: 12px 10px;
  position: relative;
  text-decoration: none;
  display: inline-block;
  &:after {
    display: block;
    content: '';
    border-bottom: solid 1px
      ${props => (props.scrolldirection === 'none' ? colors.highlight : colors.underscoreBlack)};
    transform: scaleX(0);
    transition: transform 250ms ease-in-out;
    transform-origin: 100% 50%;
  }
  &:hover {
    color: ${colors.underscoreGrey};
    &:after {
      transform: scaleX(1);
      transform-origin: 0 50%;
    }
  }
  &:focus {
    color: ${colors.underscoreBlack};
  }
`;

const Nav = () => {
  const [state, setState] = useState({
    menuOpen: false,
  });
  const [lastScrollTop, setLastScrollTop] = useState(0);
  const [bodyOffset, setBodyOffset] = useState(document.body.getBoundingClientRect());
  const [, setScrollY] = useState(bodyOffset.top);
  const [scrollDirection, setScrollDirection] = useState();

  const toggleMenu = () => setState({ ...state, menuOpen: !state.menuOpen });
  const handleScroll = () => {
    setBodyOffset(document.body.getBoundingClientRect());
    setScrollY(-bodyOffset.top);
    setScrollDirection(lastScrollTop > -bodyOffset.top ? 'up' : 'down');
    setLastScrollTop(-bodyOffset.top);
  };

  const handleResize = () => {
    if (window.innerWidth > 768 && state.menuOpen) {
      toggleMenu();
    }
  };

  const handleKeydown = e => {
    if (!state.menuOpen) {
      return;
    }

    if (e.which === 27 || e.key === 'Escape') {
      toggleMenu();
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleResize);
    window.addEventListener('keydown', e => handleKeydown(e));
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('keydown', e => handleKeydown(e));
    };
  });

  return (
    <NavContainer scrolldirection={scrollDirection}>
      <Helmet>
        <body className={state.menuOpen ? 'blur' : ''} />
      </Helmet>
      <Navbar>
        <TransitionGroup>
          {
            <CSSTransition classNames="fade" timeout={3000}>
              <Logo>
                <LogoLink href="#intro" aria-label="home">
                  <IconLogo />
                </LogoLink>
              </Logo>
            </CSSTransition>
          }
        </TransitionGroup>

        <TransitionGroup>
          {
            <CSSTransition classNames="fade" timeout={3000}>
              <Hamburger onClick={toggleMenu}>
                <HamburgerBox>
                  <HamburgerInner menuOpen={state.menuOpen} />
                </HamburgerBox>
              </Hamburger>
            </CSSTransition>
          }
        </TransitionGroup>
        <NavLinks>
          <NavList>
            <TransitionGroup>
              {navLinks &&
                navLinks.map(({ url, name }, i) => (
                  <CSSTransition key={i} classNames="fadedown" timeout={3000}>
                    <NavListItem key={i} style={{ transitionDelay: `${i * 100}ms` }}>
                      <NavLink href={url} scrolldirection={scrollDirection}>
                        {name}
                      </NavLink>
                    </NavListItem>
                  </CSSTransition>
                ))}
            </TransitionGroup>
          </NavList>
        </NavLinks>
      </Navbar>
      <Menu menuOpen={state.menuOpen} toggleMenu={toggleMenu} />
    </NavContainer>
  );
};

export default Nav;
